.wrraper {
  min-height: 100%;
}

.container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 50px 20px;
}

h1 {
  text-align: center;
}

.parallax {
  min-height: 100vh;
}

.parallax__body {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.parallax__container {
  text-transform: uppercase;
  z-index: 2;
  position: relative;
}

.parallax__label {
  color: #00f;
  align-items: center;
  margin: 0 0 3px;
  display: flex;
}

.parallax__images {
  width: 120%;
  height: 120%;
  z-index: 1;
  position: absolute;
  top: -10%;
  left: -10%;
}

.images-parallax__item {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.images-parallax__clouds, .images-parallax__human, .images-parallax__mountains {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
}

.images-parallax__clouds {
  background: url("clouds.87799955.jpg") top / cover no-repeat;
  top: 0;
}

.images-parallax__mountains {
  background: url("mountains.35f6f3ad.png") top / cover no-repeat;
  top: 15%;
}

.images-parallax__human {
  background: url("human.ead1172e.png") top / cover no-repeat;
  top: 45%;
}

@media (max-aspect-ratio: 3 / 2) {
  .images-parallax__human {
    background-position: 20% 0;
  }
}

.content {
  z-index: 2;
  background-color: #000;
  position: relative;
}

.content:before {
  content: "";
  pointer-events: none;
  width: 100%;
  height: 200px;
  background: linear-gradient(rgba(11, 29, 31, 0), #09141f);
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(0, -100%);
}

.content__title {
  color: #fff;
  margin-bottom: 30px;
  font-size: 35px;
}

.content__text {
  color: #fff;
  font-size: 16px;
  line-height: 1.5;
}

.content__text p {
  margin-bottom: 30px;
}

